import React, { useState } from "react";
import "./styles.scss";
import Wrapper from "./components/Wrapper";
import { Button } from "antd";
import di from "./di";
import { ai, Aligns, flex, fullHeight, fullWidth, jc } from "./libs/styles";
import FileInput, { FileAccept } from "./components/FileInput";
import { pureConnect } from "./libs/pureConnect";
import { FileServiceInterface } from "./services/FileService";
import {
  BookContentParserServiceInterface,
  BookFormatEnum,
} from "./services/BookContentParserService";
import { split } from "ramda";
import HumanSpeedReader from "./components/HumanSpeedReader";

interface AppInterface {
  fileService: FileServiceInterface;
  bookContentParserService: BookContentParserServiceInterface;
}
let apiprocread_cache: any[] | null = [];
if (localStorage.getItem('apiprocread_cache')) {
    let cache = localStorage.getItem('apiprocread_cache');
    if (cache !== null) {
        apiprocread_cache = JSON.parse(cache);
    }
}
function App({ fileService, bookContentParserService }: AppInterface) {
  // const [sentences, setSentences] = useState<string[][]>();
  const [sentences, setSentences] = useState<any[]>(apiprocread_cache || []);
    console.log(sentences);
    const addFile = async (file) => {
        const sentencesNew = [...sentences];
        const content = await fileService.getFileContent(file);
        const sentences1 = bookContentParserService.parse(
                content,
                BookFormatEnum.FB2
        );
        const spitedWordInSentences = sentences1.map(split(" "));
        sentences?.push(spitedWordInSentences);
        console.log(sentencesNew);
        setSentences(sentences.map(item => item));
    }
    if (!localStorage.getItem('apiprocread_cache') && sentences.length===2) {
        localStorage.setItem('apiprocread_cache', JSON.stringify(sentences))
    }
    console.log(fullHeight);
    return (
    <Wrapper
      styles={[
        flex,
        fullHeight,
        fullWidth,
        jc(Aligns.CENTER),
        ai(Aligns.CENTER),
      ]}
    >
      {sentences?.length!==2 ? (
        <>
            <FileInput
                    accept={FileAccept.BOOKS}
                    onFileLoaded={addFile}
                    view={(open) => <Button onClick={open}>Upload 1</Button>
                    }
            />
            <FileInput
                    accept={FileAccept.BOOKS}
                    onFileLoaded={addFile}
                    view={(open) => <Button onClick={open}>Upload 2</Button>
                    }
            />
        </>
      ) : (
        <HumanSpeedReader sentences={sentences[0]} sentences2={sentences[1]} />
      )}
    </Wrapper>
  );
}

export default pureConnect(
  () => ({
    fileService: di.get("fileService"),
    bookContentParserService: di.get("bookContentParserService"),
  }),
  App
);
